.main-left{
		width: 229px;
		margin-right: 16px;
		&-avatarWrapper{
			border: 1px #eee solid;
			position: relative;
			border-radius: 5px;
			overflow: hidden;
		}
		&-avatar{
			width: 229px;
			min-height: 229px;
			display: block;
			&-wrapper{
				position: relative;
				display: inline-block;
			}
			&-filter{
				z-index: 2;
				// background-color: rgba(0,0,0,.3);
				position: absolute;
				width: 100%;
				height: 100%;
				left: 0;
				top: 0;
			}
		}
		&-emoji{
			position: absolute;
			width: 28px;
			height: 28px;
			border-radius: 50%;
			margin-top: -14px;
			background-color: #fff;
			z-index: 2;
			left: 15px;
			text-align: center;
			line-height: 28px;
		}
		&-signature{
			line-height: 50px;
			white-space: normal;
			font-weight: 100;
			display: block;
			height: 50px;
			box-sizing: border-box;
			padding: 8px;
			color: #24292e;
		}
		&-name{
			margin-top:16px;
			margin-bottom: 16px;
			display: block;
			&>span{
				display: block;
			}
		}
		&-nickname{
			color: #666;
			font-size: 20px;
			font-style: normal;
			font-weight: 300;
			line-height: 24px;
			margin-top: -5px;
		}
		&-location{
			color: #24292e;
			font-size: 14px;
			text-decoration: none;
			&:active{
				color: #24292e;
			}
		}
		& em{
			margin-right: 5px
		}
}
.line{
	border:0;
	border-bottom: 1px #eee solid;
	width: 100%;
}
.tag{
	padding:3px 4px;
	border-radius: 3px;
	display: inline-block;
	color: #fff;
	font-size: 12px;
	line-height: 1;
	font-weight: 600;
	box-shadow: inset 0 -1px 0 rgba(27,31,35,.12);
}
.green{
	background-color: rgb(21, 77, 21);
}
.red{
	background-color: rgb(235, 71, 1);
}
.black{
	background-color: rgb(36, 36, 36);
}
.blue{
	background-color: rgb(2, 50, 174);
}